/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  background-color: #fff;
  color: #010101;
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.imagen-small {
  width: 20rem;
}

.ocultar {
  display: none;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #fff;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

.login {
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.login .tarjeta {
  margin: auto 5rem;
  width: 100%;
}
.login .tarjeta .titulo {
  margin: 5rem auto;
  font-size: 5rem;
  text-transform: uppercase;
  font-weight: 700;
}

.logo {
  display: flex;
  justify-content: center;
}
.logo img {
  width: 50%;
}

.boton-iniciar {
  background-color: #197fd5;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
}
@media (min-width: 769px) {
  .boton-iniciar {
    max-width: 95%;
    margin: 0 auto;
  }
}
.boton-iniciar:hover {
  background-color: #1464a7;
  cursor: pointer;
}

.formulario {
  margin: 2.5rem 0;
}
.formulario .campo {
  display: block;
  margin: 1rem;
}
@media (min-width: 769px) {
  .formulario .campo {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.formulario .campo label {
  font-size: 2rem;
  text-transform: capitalize;
  margin-right: 2rem;
}
.formulario .campo input,
.formulario .campo select,
.formulario .campo textarea {
  flex: 1;
  display: block;
  width: 100%;
  border: none;
  border-radius: 0.5rem;
  background-color: #e6e6e6;
  padding: 1rem 0;
  font-size: 1.5rem;
  text-transform: capitalize;
}

.alerta {
  padding: 1rem;
  text-align: center;
  margin: 1rem 0;
  border-radius: 0.5rem;
  color: #fff;
  text-transform: capitalize;
  font-size: 300;
}
.alerta.error {
  background-color: #e83232;
}
.alerta.exito {
  background-color: #197fd5;
}

.sidebar {
  background-color: #197fd5;
  padding: 2rem;
}
.sidebar h2 {
  color: #fff;
  margin: 0;
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
}
.sidebar .sidebar-nav {
  margin-top: 6rem;
}
.sidebar .sidebar-nav a {
  color: #fff;
  display: block;
  margin-bottom: 1.5rem;
  padding: 0.7rem;
  transition-property: background-color;
  transition-duration: 300ms;
  font-size: 1.5rem;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #1464a7;
  border-radius: 0.8rem;
}

.imagen {
  height: auto;
  width: 20rem;
  margin: 2rem 0;
}
.imagen img {
  max-width: 100%;
  max-height: 100%;
}

.dashboard {
  background-color: #f4f4f4;
}
@media (min-width: 769px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
}
.dashboard .contenido-pagina {
  padding: 2rem;
}

.botoncrear {
  display: flex;
  justify-content: right;
}
.botoncrear .crear-cliente {
  padding: 1rem;
  color: #fff;
  background-color: #197fd5;
  font-size: 2rem;
  font-weight: 700;
  margin: 1rem;
  transition-property: background-color border-radius;
  transition-duration: 300ms;
}
.botoncrear .crear-cliente:hover {
  background-color: #1464a7;
  border-radius: 0.5rem;
}

@media (min-width: 769px) {
  .clientes {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1600px) {
  .clientes {
    grid-template-columns: repeat(3, 1fr);
  }
}
.clientes .cliente {
  margin: 2rem 0;
  background-color: #3998e8;
  padding: 1rem;
  transition-property: all;
  transition-duration: 300ms;
}
.clientes .cliente .nit-cliente {
  text-align: center;
  font-weight: 700;
  color: #fff;
}
@media (min-width: 1240px) {
  .clientes .cliente .datos-cliente {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0 2rem;
  }
}
.clientes .cliente p {
  text-align: center;
  line-height: 2.5rem;
  text-transform: capitalize;
}
.clientes .cliente:hover {
  border-radius: 1rem;
  background-color: #1464a7;
}

.divBoton {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
}
.divBoton .editar-cliente {
  padding: 1rem;
  background-color: #e3d617;
  color: #f2f2f2;
  font-size: 2rem;
  font-weight: 700;
  transition-property: background-color border-radius;
  transition-duration: 300ms;
}
.divBoton .editar-cliente:hover {
  background-color: #b5aa12;
  border-radius: 0.5rem;
}

.volver {
  display: block;
  text-align: center;
  padding: 1rem 2.5rem;
  color: #fff;
  background-color: #197fd5;
  font-size: 2rem;
  font-weight: 700;
  margin: 1rem;
  transition-property: background-color border-radius;
  transition-duration: 300ms;
}
@media (min-width: 769px) {
  .volver {
    display: inline-block;
  }
}
.volver:hover {
  background-color: #1464a7;
  border-radius: 0.5rem;
}

.boton-general {
  display: flex;
  justify-content: center;
}
.boton-general button[type=submit] {
  border: none;
  padding: 1rem 2.5rem;
  color: #fff;
  background-color: #197fd5;
  font-size: 2rem;
  font-weight: 700;
  margin: 1rem;
  transition-property: background-color border-radius;
  transition-duration: 300ms;
}
.boton-general button[type=submit]:hover {
  background-color: #1464a7;
  border-radius: 0.5rem;
  cursor: pointer;
}

@media (min-width: 1240px) {
  .productos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.productos .producto {
  background-color: #3998e8;
  margin: 2rem;
  padding: 1rem;
  transition-property: all;
  transition-duration: 300ms;
}
.productos .producto .imagen {
  margin: 1.5rem auto;
  width: 25rem;
  height: 25rem;
}
.productos .producto .imagen img {
  width: 100%;
  border-radius: 13rem;
  transition-property: border-radius;
  transition-duration: 300ms;
}
.productos .producto .imagen img:hover {
  border-radius: 0;
}
.productos .producto h3 {
  color: #fff;
  font-size: 2.5rem;
  font-weight: 700;
  text-transform: capitalize;
}
.productos .producto .codigo {
  font-size: 2rem;
}
.productos .producto p {
  font-size: 1.5rem;
}
.productos .producto .datos-precio {
  padding: 1rem;
  text-align: center;
}
@media (min-width: 769px) {
  .productos .producto .datos-precio {
    display: flex;
    justify-content: space-evenly;
  }
}
.productos .producto .datos-precio h4 {
  color: #fff;
  font-size: 2.1rem;
}
.productos .producto:hover {
  border-radius: 1rem;
  background-color: #1464a7;
}

h2 {
  text-transform: capitalize;
}

.accion {
  margin: 1rem 0;
  display: block;
  text-align: center;
}
@media (min-width: 769px) {
  .accion {
    display: flex;
    justify-content: center;
  }
}
.accion .editar {
  padding: 1rem 2rem;
  background-color: #e3d617;
  color: #f2f2f2;
  font-size: 2rem;
  font-weight: 700;
  transition-property: background-color color;
  transition-duration: 300ms;
}
.accion .editar:hover {
  background-color: #867f0e;
  color: #e6e6e6;
  border-radius: 0.5rem;
}

.botones-superior {
  margin: 2rem 0;
}
@media (min-width: 769px) {
  .botones-superior {
    display: flex;
    justify-content: space-around;
  }
}
.botones-superior a {
  padding: 1rem;
  text-align: center;
  background-color: #197fd5;
  color: white;
  font-size: 2rem;
  display: block;
  margin: 1rem;
  font-weight: 700;
  transition-property: all;
  transition-duration: 300ms;
}
.botones-superior a:hover {
  background-color: #1464a7;
  border-radius: 0.5rem;
}

.buscador {
  display: flex;
  justify-content: space-between;
}
.buscador input[type=search] {
  flex: 1;
  display: block;
  width: 100%;
  border: none;
  border-radius: 0.5rem;
  background-color: #e6e6e6;
  padding: 1rem 0;
  font-size: 1.5rem;
}
.buscador button {
  background-color: #197fd5;
  color: white;
  font-size: 2rem;
  border: none;
  border-radius: 0.5rem;
  transition-property: all;
  transition-duration: 300ms;
}
.buscador button:hover {
  cursor: pointer;
  border-radius: 0;
}

.usuarios {
  margin-top: 2rem;
}
@media (min-width: 769px) {
  .usuarios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2;
  }
}
@media (min-width: 1600px) {
  .usuarios {
    grid-template-columns: repeat(3, 1fr);
  }
}
.usuarios .usuario {
  background-color: #3998e8;
  margin: 1rem;
  padding: 1rem;
  text-align: center;
  transition-property: all;
  transition-duration: 300ms;
}
@media (min-width: 769px) {
  .usuarios .usuario {
    padding: 2rem;
  }
}
.usuarios .usuario h3 {
  font-weight: 700;
  color: #fff;
  font-size: 2.5rem;
  text-transform: capitalize;
}
.usuarios .usuario p {
  font-size: 1.5rem;
  text-transform: capitalize;
}
@media (min-width: 769px) {
  .usuarios .usuario p {
    font-size: 2rem;
  }
}
.usuarios .usuario:hover {
  border-radius: 0.5rem;
  background-color: #1464a7;
}