@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}

body {
    min-height: 100%;
    background-color: v.$blanco;
    color: v.$negro;
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}

*, *:before, *:after {
    box-sizing: inherit;
} 


.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
}

.imagen-small {
    width: 20rem;
}

.ocultar {
    display: none;
}